import { create } from "zustand";
import { persist } from "zustand/middleware";
import { authApi } from "@/lib/api";

export interface User {
  id: string;
  email: string;
  name?: string;
}

interface AuthStore {
  user: User | null;
  token: string | null;
  isLoading: boolean;
  error: string | null;
  
  // Actions
  login: (email: string, password: string) => Promise<boolean>;
  register: (email: string, password: string, name?: string) => Promise<boolean>;
  logout: () => void;
  setError: (error: string | null) => void;
  clearError: () => void;
}

export const useAuthStore = create<AuthStore>()(
  persist(
    (set) => ({
      user: null,
      token: null,
      isLoading: false,
      error: null,

      login: async (email: string, password: string) => {
        set({ isLoading: true, error: null });
        const response = await authApi.login(email, password);
        
        if (response.error) {
          set({ 
            error: response.error.message, 
            isLoading: false,
            user: null,
            token: null,
          });
          return false;
        }

        if (response.data) {
          set({
            user: response.data.user,
            token: response.data.token,
            isLoading: false,
            error: null,
          });
          return true;
        }
        
        set({ isLoading: false });
        return false;
      },

      register: async (email: string, password: string, name?: string) => {
        set({ isLoading: true, error: null });
        const response = await authApi.register(email, password, name);
        
        if (response.error) {
          // √ñzel durum: Kullanƒ±cƒ± olu≈üturuldu ama email g√∂nderilemedi
          // Backend 500 hatasƒ± d√∂nd√ºrse bile, eƒüer "User created" mesajƒ± varsa ba≈üarƒ±lƒ± say
          const errorMessage = (response.error.message || "").toLowerCase();
          const errorStatus = response.error.status;
          
          // 500 hatasƒ± ve "user created" i√ßeriyorsa veya sadece "user created" i√ßeriyorsa
          if (errorMessage.includes("user created") || 
              errorMessage.includes("kullanƒ±cƒ± olu≈üturuldu") ||
              (errorStatus === 500 && errorMessage.includes("failed to send"))) {
            set({
              isLoading: false,
              error: null,
            });
            return true;
          }
          
          set({ 
            error: response.error.message, 
            isLoading: false,
            user: null,
            token: null,
          });
          return false;
        }

        set({
          isLoading: false,
          error: null,
        });
        return true;
      },

      logout: () => {
        set({
          user: null,
          token: null,
          error: null,
        });
      },

      setError: (error: string | null) => set({ error }),
      clearError: () => set({ error: null }),
    }),
    {
      name: "auth-storage",
      partialize: (state: AuthStore) => ({ 
        user: state.user, 
        token: state.token 
      }),
    }
  )
);

